const ru = (content) => {
    // console.log(content);

    switch (content) {
        case 'News':
            return 'Новости'
        case 'Allnews':
            return 'Все Новости'
        case 'programs':
            return 'Программы'
        case 'travels':
            return 'Путешествовать по миру'
        case 'gallery':
            return 'Галерея'
        case 'gallery-desc':
            return 'Dunyo Bo\'ylap Галерея'
        case 'bio':
            return 'Вместе с вами по всему миру!'
        case 'address':
            return 'Ул. А. Навои, 69, 100011, Ташкент'
        case 'rights':
            return "Телеканал Dunyo Bo'ylab. Все права защищены. Копирование материалов без согласования запрещено."
        case 'shows':
            return "Телепередачы"
        case 'weekly':
            return "Еженедельные телешоу"
        case 'world':
            return "Вокруг света"
        case 'contact':
            return "Контакт"
        case 'read-more':
            return "Подробнее"
        case 'index-title':
            return "Dunyo Bo'ylab | Официальный телеканал"
        case 'index-desc':
            return "Официальный сайт телеканала Dunyo Boylap при Национальной телерадиокомпании Узбекистана"
        case 'recent-news':
            return "Последние новости"
        case 'recent-travels':
            return "Недавние поездки"
        case 'allnews-desc':
            return "Новости со всего мира"
        default:
            break;
    }
}

export { ru }